import { Link } from "gatsby"
import React from "react"

const menu = require("./../menu.json");
const Header = () => (
  <header
    style={{
      background: `rgba(68,218,179,1)`,
    }}
  >
    <div
      className="header-wrap"
    >
      <div
        className="logo"
        style={{
          margin: `0 1rem`,
          color: `white`,
          fontSize: `30px`,
          fontFamily: `Fantasy`
        }}
      >
        z7xisoo
      </div>

      <div
        style={{
          display: `flex`,
        }}
      >
        {
          menu.map((item, key) => (
              <Link
                key={key}
                to={item.path}
                style={{
                  color: `white`,
                  textDecoration: `none`,
                  alignItems: `center`,
                  margin: `0 .3rem`,
                  display: `flex`,
                  fontWeight: `bolder`,
                }}
                title={item.content}
              >
                {item.title}
              </Link>
          ))
        }
      </div>
    </div>
  </header>
)
export default Header
